.links {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  @include r(960) {
    width: 100%; }
  &__link {
    &:hover {
      text-decoration: none; } }
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    // &:not(:last-child)
    //   margin-right: 10px

    &:hover {
      .links__text {
        color: $green; } } }
  &__pic {
    display: inline-block;
    vertical-align: top;
    width: 30px;
    height: 30px;
    margin-top: -5px;
    // +r(960)
    //   mr-2
    //   width: 25px
    //   height: 25px
 }    //   margin-top: -2px
  &__text {
    display: inline-block;
    vertical-align: middle;
    font-size: 17px;
    color: $borde;
    @include rob-l;
    @include r(960) {
      color: $green;
      font-size: 12px;
 } }      // font-size: 0
  &__mob {
    display: none;
    text-decoration: underline;
    @include r(960) {
      display: inline-block;
      font-size: 12px;
      color: $green; } }

  &__dropdown {
    &.is-open {
      .links__dropdown-list {
        display: block; } } }
  &__dropdown-arr {
    position: absolute;
    background: url('../assets/img/general/select-top-arr.png') no-repeat 10px 0;
    width: 36px;
    height: 14px;
    left: 19px;
    top: 4px; }

  &__dropdown-list {
    display: none;
    position: absolute;
    z-index: 99;
    left: 50%;
    transform: translateX(-25%);
    padding-top: 15px;
    top: 100%;
    @include r(960) {
      right: auto;
      left: 0;
      transform: none; }
    ul {
      border: 2px solid #bcbfc4;
      background: #fff;
      height: 350px;
      overflow-y: scroll;
      min-width: 90px; }
    li {
      padding: 7px;
      min-width: 70px;
      &:hover {
        background: $green;
        a {
          color: #fff; } } }
    a {
      color: #000; } }
  &__item_cont {
    @include r(960) {
      display: none; } }
  &__item_marker {
    @include r(960) {
      margin-right: 0;
      float: left; }

    .links__text {
      @include r(960) {
        font-size: 0;
        .uppercase {
          font-size: 14px;
          color: $green;
          text-decoration: underline; } } } }
  &__item_basket {
    @include r(960) {
      margin-right: 100px;
      float: right; } }

  &__item_user {
    @include r(960) {
      position: absolute;
      right: 0; } } }
